<template>
  <div class="profile">
    <div class="user-info">
      <button @click="logout">Logout</button>
      <h2>User Profile</h2>
      <p v-if="responseData && responseData.data.user.length > 0">Username: {{ responseData.data.user[0].login }}</p>
      <p v-if="xpAmount !== null">XP Amount: {{ (xpAmount / 1000).toFixed(0) }} KB</p>
      <p v-else>XP Amount: N/A</p>
      <p v-if="loading">Loading...</p>
      <p v-if="error">{{ error.message }}</p>
    </div>
    <div class="audits-section">
      <div class="mt2-01 flexColumn-01">
        <div class="mb3-01 fs4-01">Audits</div>
        <div class="fs4-01 fMono-01" v-if="hasAudits">There are audits to do.</div>
        <div class="fs4-01 fMono-01" v-else>No audit to do, you're good!</div>
        <div class="grey4-01" v-if="hasAudits"></div>
      </div>
    </div>
    <div class="graphs">
      <h2>XP progression</h2>
      <svg class="graph" width="600" height="300" ref="xpChart"></svg>
    </div>
    <div class="audit-ratio-graph">
      <h2>Audit Ratio</h2>
      <div class="audit-ratio-values">
        <div class="audit-ratio-ratio">{{ auditRatio.toFixed(1) }}</div>
      </div>
      <svg class="graph" width="800" height="400" ref="auditRatioChart"></svg>
      <div class="audit-ratio-done">Done</div>
      <div class="audit-ratio-value">{{ (Math.floor(auditsDone / 1000000 * 100) / 100).toFixed(2) }} MB</div>
      <div class="audit-ratio-received">Received</div>
      <div class="audit-ratio-value">{{ (Math.ceil(auditsReceived / 1000000 * 100) / 100).toFixed(2) }} MB</div>
    </div>
  </div>
</template>

<script>
import * as d3 from 'd3';
import axios from 'axios';

export default {
  name: 'ProfileView',
  data() {
    return {
      responseData: null,
      loading: false,
      error: null,
      xpAmount: null,
      hasAudits: false,
      lineChartData: [],
      xScale: null,
      yScale: null,
      auditsReceived: null,
      auditsDone: null,
    };
  },
  computed: {
    auditRatio() {
      if (this.responseData && this.responseData.data && this.responseData.data.user && this.responseData.data.user.length > 0) {
        const user = this.responseData.data.user[0];
        const totalUp = user.totalUp || 0;
        const totalDown = user.totalDown || 0;

        const ratio = totalUp / totalDown;

        return isNaN(ratio) ? 0 : ratio;
      } else {
        return 0;
      }
    },
  },
  methods: {
    fetchData() {
      this.loading = true;
      const jwtToken = localStorage.getItem('jwt');
      axios
        .post(
          'https://01.kood.tech/api/graphql-engine/v1/graphql',
          JSON.stringify({
            query: `
            {
              user {
                id
                login
                attrs
                totalUp
                totalDown
                createdAt
                updatedAt
                transactions(where: {
                  _and: [
                    { type: { _iregex: "xp|level" } }
                    { path: { _iregex: "div-01" } }
                    { path: { _niregex: "piscine" } }
                  ]
                }, order_by: { createdAt: asc }) {
                  id
                  userId
                  type
                  amount
                  createdAt
                  path
                }
              }
            }`,
          }),
          {
            headers: {
              Authorization: `Bearer ${jwtToken}`,
              'Content-Type': 'application/json',
            },
          }
        )
        .then((response) => {
          this.responseData = response.data;
          if (this.responseData && this.responseData.data.user.length > 0) {
            const user = this.responseData.data.user[0];
            this.xpAmount = user.transactions
              .filter((transaction) => transaction.type === 'xp')
              .reduce((acc, transaction) => acc + transaction.amount, 0);

            this.lineChartData = this.formatDataForChart(user.transactions);
            this.auditsReceived = user.totalDown || 0;
            this.auditsDone = user.totalUp || 0; // Set auditsDone
          }
        })
        .catch((error) => {
          this.error = error;
        })
        .finally(() => {
          this.loading = false;
          this.generateXPChart();
          this.generateAuditRatioChart();
        });

      if (
        this.responseData &&
        this.responseData.data.user.length > 0 &&
        this.responseData.data.user[0].audits &&
        this.responseData.data.user[0].audits.length > 0
      ) {
        this.hasAudits = true;
      } else {
        this.hasAudits = false;
      }
    },

    formatDataForChart(transactions) {
      let accumulatedXP = 0;
      return transactions
        .filter((transaction) => transaction.type === 'xp')
        .map((transaction) => {
          accumulatedXP += parseFloat(transaction.amount);
          return {
            date: new Date(transaction.createdAt),
            xp: accumulatedXP,
          };
        });
    },

    generateXPChart() {
      if (!this.lineChartData.length) {
        return; // Don't generate the chart if there's no data
      }

      const margin = { top: 40, right: 20, bottom: 30, left: 40 };
      const width = 800 - margin.left - margin.right;
      const height = 400 - margin.top - margin.bottom;

      const svg = d3.select(this.$refs.xpChart);
      svg.selectAll('g').remove(); // Clear any existing chart elements within the 'g' group.

      // Create the 'g' group for the chart elements
      svg.append('g')
        .attr('transform', `translate(${margin.left},${margin.top})`);

      // Convert KB to MB in the Y-axis scale
      this.xScale = d3
        .scaleTime()
        .domain(d3.extent(this.lineChartData, (d) => d.date))
        .range([0, width]);

      this.yScale = d3
        .scaleLinear()
        .domain([0, d3.max(this.lineChartData, (d) => d.xp / 1024)]) // Adjust the max value and add some padding
        .nice()
        .range([height, 0]);

      const line = d3
        .line()
        .x((d) => this.xScale(d.date))
        .y((d) => this.yScale(d.xp / 1024)); // Convert KB to MB

      svg
        .attr('width', width + margin.left + margin.right)
        .attr('height', height + margin.top + margin.bottom)
        .append('g')
        .attr('transform', `translate(${margin.left},${margin.top})`);

      // Create x and y axes
      const xAxis = d3.axisBottom(this.xScale);

      // Configure xAxis to display months
      xAxis.tickFormat(d3.timeFormat('%b %Y'));

      // Set Y-axis ticks to show integer values
      const yAxis = d3.axisLeft(this.yScale)
        .tickValues([100, 200, 300, 400, 500]) // Add the specific values you want to mark
        .tickFormat((d) => d + ' KB');

      // Format the Y-axis tick labels to include "MB"
      yAxis.tickFormat((d) => d + ' MB');

      // Create a numerical scale on the left side
      // Create the path for the line chart
      svg
        .append('path')
        .datum(this.lineChartData)
        .attr('fill', 'none')
        .attr('stroke', 'steelblue')
        .attr('stroke-width', 2)
        .attr('d', line);

      // Create and position the X-axis
      svg
        .append('g')
        .attr('class', 'x-axis')
        .attr('transform', `translate(0, ${height})`)
        .call(xAxis);

      // Create and position the Y-axis
      svg
        .append('g')
        .attr('class', 'y-axis')
        .call(yAxis)
        .selectAll('text')
        .style('text-anchor', 'end')
        .style('font-weight', 'bold')
        .attr('dx', '-0.5em') // Adjust the position of the labels
        .attr('dy', '0.15em');
    },

    formatAuditRatioData(auditData) {
      if (auditData) {
        return auditData.map((entry) => ({
          timestamp: new Date(entry.timestamp), // Parse the timestamp
          ratio: entry.completed / entry.total, // Calculate the ratio
        }));
      } else {
        return []; // Return an empty array if auditData is undefined
      }
    },

    generateAuditRatioChart() {
      if (!this.auditsReceived) {
        return;
      }

      const margin = { top: 40, right: 20, bottom: 30, left: 40 };
      const width = 800 - margin.left - margin.right;
      const height = 400 - margin.top - margin.bottom;

      const svg = d3.select(this.$refs.auditRatioChart);
      svg.selectAll('*').remove();

      const xScale = d3.scaleBand()
        .domain(['Done', 'Received'])
        .range([0, width])
        .padding(0.2);

      const yScale = d3.scaleLinear()
        .domain([0, d3.max([this.auditsDone, this.auditsReceived])]) // Use d3.max to find the max value
        .nice()
        .range([height, 0]);

      svg
        .attr('width', width + margin.left + margin.right)
        .attr('height', height + margin.top + margin.bottom)
        .append('g')
        .attr('transform', `translate(${margin.left},${margin.top})`);

      // Create and position the X-axis
      const xAxis = d3.axisBottom(xScale);
      svg
        .append('g')
        .attr('class', 'x-axis')
        .attr('transform', `translate(0, ${height})`)
        .call(xAxis);

      // Create and position the Y-axis
      svg
        .append('g')
        .attr('class', 'y-axis')
        .call(d3.axisLeft(yScale));

      // Create data for "Done" and "Received" bars
      const barsData = [
        { category: 'Done', value: this.auditsDone },
        { category: 'Received', value: this.auditsReceived }
      ];

      // Create the bars
      svg.selectAll('rect')
        .data(barsData)
        .enter()
        .append('rect')
        .attr('x', (d) => xScale(d.category))
        .attr('y', (d) => yScale(d.value))
        .attr('width', xScale.bandwidth())
        .attr('height', (d) => height - yScale(d.value))
        .attr('fill', 'steelblue');

      // Add labels for the bars
      svg.selectAll('text')
        .data(barsData)
        .enter()
        .append('text')
        .text((d) => d.value + ' MB')
        .attr('x', (d) => xScale(d.category) + xScale.bandwidth() / 2)
        .attr('y', (d) => yScale(d.value) - 10)
        .attr('text-anchor', 'middle')
        .attr('font-size', '14px')
        .attr('fill', 'white'); // 

      const ratioText = 'Ratio: ' + (this.auditsDone / this.auditsReceived).toFixed(1);
      svg
        .append('text')
        .text(ratioText)
        .attr('x', width / 2)
        .attr('y', height + margin.bottom)
        .attr('text-anchor', 'middle')
        .attr('font-size', '16px')
        .attr('fill', 'white');
    },

    logout() {
      localStorage.removeItem('jwt');
      this.$router.push('/');
    },
  },

  mounted() {
    this.fetchData();
  },
};

</script>

